import { Global } from './../../common/global';
import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { ClrLoadingState } from '@clr/angular';
import { UsersService } from './../../service/users.service';



@Component({
  selector: 'app-sync-settings',
  templateUrl: './sync-settings.component.html',
  styleUrls: ['./sync-settings.component.scss'],
})
export class SyncSettingsComponent implements OnInit {
hasSchedule: boolean = false;
users = [];
groups = [];
frequency;
currentSyncSchedule;
currentSyncDay;
currentSyncTime;

day;
time;

divhide:boolean = false;
SyncDiv:boolean = false;
SyncDivConfirm:boolean = false;

syncSchedule;
userSync;
groupSync;
groupStr;
userStr;

groupsSelected = [];
usersSelected = [];
usersSelectedArray = [];
userSyncType
groupSyncType

UserSaveBtnState: ClrLoadingState = ClrLoadingState.DEFAULT;
UserModifyBtnState: ClrLoadingState = ClrLoadingState.DEFAULT;

GroupSaveBtnState: ClrLoadingState = ClrLoadingState.DEFAULT;
GroupModifyBtnState: ClrLoadingState = ClrLoadingState.DEFAULT;

GoogleGroups = [];
GoogleGroupsSearchBtn: ClrLoadingState = ClrLoadingState.DEFAULT;

GoogleUsers = [];
GoogleUsersSearchBtn: ClrLoadingState = ClrLoadingState.DEFAULT;
GoogleUsersSearch:boolean = true;

isReadOnly:boolean = false;

success: boolean = false;
error: boolean = false;

hasAlert = this._global.hasAlert;

userSyncSet:boolean
groupSyncSet:boolean
schedSyncSet:boolean


userSyncModal:boolean = false;
groupSyncModal:boolean = false;
setSyncModal:boolean = false;
clearConfigModal:boolean = false;

userSyncAll:boolean = false;
groupSyncAll:boolean = false;

syncschedpostbody = {};
syncuserpostbody = {};
syncgrouppostbody = {};

result;

constructor(private usersService: UsersService, private _router: Router, private _global: Global, public pubglobal: Global ) { }

ngOnInit() : void  {
  
  this.usersService.getSyncSchedule()
  .subscribe(
  body => { this.syncSchedule = body;
    console.log(this.syncSchedule)
  if(this.syncSchedule.body[0].synctype.includes('schedule')){this.schedSyncSet = true} else {this.schedSyncSet = false}

  },
  error => { console.log('error', error)},
  )


  this.usersService.getUserSync()
  .subscribe(
  body => { this.userSync = body;

  if(this.userSync.body[0].synctype.includes('user')){this.userSyncSet = true} if(this.userSync.body[0].synctype.includes('userpartial')){this.userSyncSet = true} else {this.userSyncSet = false}
    this.concatUser()

  },
  error => { console.log('error', error)},
  )


  this.usersService.getGroupSync()
  .subscribe(
  body => { this.groupSync = body;
  if(this.groupSync.body[0].synctype.includes('group')){this.groupSyncSet = true} if(this.groupSync.body[0].synctype.includes('grouppartial')){this.groupSyncSet = true} else {this.groupSyncSet = false}
    this.concatGroup()
  },
  error => { console.log('error', error)},
  )
  this.hideNotifications();
} 

concatUser() {
  if (this.userSync.body[0].syncvalues.length > 25) {
    this.userStr = this.userSync.body[0].syncvalues.substring(0, 10) + "...";
  } 
}

concatGroup() {
  if (this.groupSync.body[0].syncvalues.length > 25) {
    this.groupStr = this.groupSync.body[0].syncvalues.substring(0, 10) + "...";
  }
}
reloadComponent() {
  this._router.routeReuseStrategy.shouldReuseRoute = () => false;
  this._router.onSameUrlNavigation = 'reload';
  this._router.navigate(['/config/sync-settings']);
}


delay(ms: number) {
  return new Promise( resolve => setTimeout(resolve, ms) );
}

async hideNotifications() {
  await this.delay(3000);
  this._global.hasAlert = false;
}

onListUsersClick(){
  this.GoogleUsers = [];

  this.GoogleUsersSearchBtn = ClrLoadingState.LOADING;
    this.usersService.getGoogleUsers()
    .subscribe(
      body => { this.GoogleUsers = body, this.GoogleUsersSearchBtn = ClrLoadingState.SUCCESS; },
      error => { console.log('error', error), this.GoogleUsersSearchBtn = ClrLoadingState.ERROR;}
      )
}

onListGroupsClick(){
this.GoogleGroups = [];
this.groupsSelected = [];
this.GoogleGroupsSearchBtn = ClrLoadingState.LOADING;
    this.usersService.getGoogleGroups()
    .subscribe(
      body => { this.GoogleGroups = body, this.GoogleGroupsSearchBtn = ClrLoadingState.SUCCESS; },
      error => { console.log('error', error), this.GoogleGroupsSearchBtn = ClrLoadingState.ERROR;}
      )  
}

onSyncAllUsersClick(){
  this.usersSelected = ['all']
  this.userSyncType = 'userall'
  this.userSyncAll = true
  this.userSyncModal = true
}

onSyncAllGroupsClick(){
  this.groupsSelected = ['all']
  this.groupSyncType = 'groupall'
  this.groupSyncAll = true
  this.groupSyncModal = true
}

onSyncSelectedUsersClick(){
  this.userSyncModal = true
}

onSyncSelectedGroupsClick(){
  this.groupSyncModal = true  
}

onSetSyncClick(){
  this.setSyncModal = true
}

onConfirmSyncClick(){
 this.syncschedpostbody = {
    "synctype": 'schedule',
    "syncvalues": null,
    "frequency": this.frequency,
    "day": this.day,
    "time": this.time
  };
  this.usersService.saveSyncSchedule(this.syncschedpostbody)
    .subscribe(
      body => { this.result = body, this._global.success},
      error => { console.log('error', error), this._global.error = true}
      )
    this.reloadComponent()
    this.setSyncModal = false
    this._global.hasAlert = true
    this.reloadComponent()
    this.hideNotifications()
}

onConfirmClearConfig(){
  this.usersService.clearSyncConfig()
  .subscribe(
    body => { this.result = body, this._global.success},
    error => { console.log('error', error), this._global.error = true}
    );
    this.clearConfigModal = false
    this._global.hasAlert = true
    this.reloadComponent()
    this.hideNotifications()   
}

onGroupsSubmitClick(){
  this.GoogleGroupsSearchBtn = ClrLoadingState.LOADING;
  this.usersService.getGoogleGroups()
  .subscribe(
    body => { this.GoogleGroups = body, this.GoogleGroupsSearchBtn = ClrLoadingState.SUCCESS; },
    error => { console.log('error', error)}
    )  
}

onConfirmUserSync(){
  let userArray = [];
  this.usersSelected.forEach(data => {
    userArray.push( data.primaryEmail  )
  }) 
  this.syncuserpostbody = {
    "synctype": 'userpartial',
    "syncvalues": userArray,
    "frequency": null,
    "day": null,
    "time": null
  };
  this.usersService.saveSyncUsers(this.syncuserpostbody)
    .subscribe(
     body => { this.result = body, this._global.success},
     error => { console.log('error', error), this._global.error = true}
     )
  
  this._global.hasAlert = true
  this.divhide = true;
  this.reloadComponent()
  this.hideNotifications()

}

onConfirmGroupSync(){
  let groupArray = [];
  this.groupsSelected.forEach(data => {
    groupArray.push( data.id )
  }) 

  this.syncgrouppostbody = {
    "synctype": 'grouppartial',
    "syncvalues": groupArray
};

this.usersService.saveSyncGroups(this.syncgrouppostbody)
  .subscribe(
    body => { this.result = body, this._global.success},
    error => { console.log('error', error), this._global.error = true}
    )
  this.groupSyncModal = false
  this._global.hasAlert = true
  this.reloadComponent()
  this.hideNotifications()
}

}



