import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CredentialsService } from './../service/credentials.service';


@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {
  noconfig: boolean = false
  result: any
  status = []
  error:any
  accessError: boolean = false
  googleError: boolean = false
  accessSuccess: boolean = false
  googleSuccess: boolean = false
  constructor(private _router: Router, private credentialsService: CredentialsService) { }

  async ngOnInit() {
    this.credentialsService.ping()
    .subscribe(
      body => { this.result = body},
      error => { console.log('error', error), this.noconfig = true;}
      )

   this.credentialsService.status()
    .subscribe(
      body => { 
        this.status.push(body);
        if(this.status[0].google == "true"){this.googleSuccess = true}
        if(this.status[0].google != "true"){this.googleError = true}
        if(this.status[0].access == "true"){this.accessSuccess = true}
        if(this.status[0].access != "true"){this.accessError = true}        
        },
      error => {console.log('error', error), this.error = true }
      )        
  }

  configure() {
    this._router.navigate(['/config']);
  }

  reloadComponent() {
    this._router.routeReuseStrategy.shouldReuseRoute = () => false;
    this._router.onSameUrlNavigation = 'reload';
    this._router.navigate(['/']);
  }
}
