import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';



@Injectable({
  providedIn: 'root'
})

export class UsersService {
getError;
getSuccess;
isError: boolean;
isSuccess: boolean;

constructor(private httpClient: HttpClient) { }
headers = new HttpHeaders()
  .set('Content-Type', 'application/json')
  .set('Accept', 'application/json')
  .set('Access-Control-Allow-Origin', '*')
  .set('rejectUnauthorized', 'false')

// .set('Access-Control-Allow-Origin', '*');
options = {
    observe: 'response' as 'body',
  };

public getGoogleUsers(): Observable<any[]> {
  return this.httpClient.get<any[]>('http://localhost:8080/allusers', { headers: this.headers });
}
public getAccessUsers(): Observable<any[]> {
  return this.httpClient.get<any[]>('http://localhost:8080/allaccessusers', { headers: this.headers });
}

public getGoogleGroups(): Observable<any[]> {
  return this.httpClient.get<any[]>('http://localhost:8080/allgroups', { headers: this.headers });
}

public getAccessGroups(): Observable<any[]> {
  return this.httpClient.get<any[]>('http://localhost:8080/allaccessgroups', { headers: this.headers });
}

public searchGoogleUsers(email): Observable<any[]> {
  return this.httpClient.post<any[]>('http://localhost:8080/searchgoogleusers', email, this.options);
}

public searchAccessUsers(email): Observable<any[]> {
  return this.httpClient.post<any[]>('http://localhost:8080/searchaccessusers', email, this.options);
}

public searchGoogleGroups(name): Observable<any[]> {
  let body = {name}
  return this.httpClient.post<any[]>('http://localhost:8080/searchgooglegroups', body, this.options);
}

public searchAccessGroups(name): Observable<any[]> {
  return this.httpClient.post<any[]>('http://localhost:8080/searchaccessgroups', name, { headers: this.headers });
}

public saveSyncSchedule(syncschedpostbody): Observable<any[]> {
  return this.httpClient.post<any[]>('http://localhost:8080/savesyncschedule', syncschedpostbody, this.options);
}

public saveSyncUsers(syncuserpostbody): Observable<any[]> {
  return this.httpClient.post<any[]>('http://localhost:8080/savesyncusers', syncuserpostbody, this.options);
}

public saveSyncGroups(syncgrouppostbody): Observable<any[]> {
  return this.httpClient.post<any[]>('http://localhost:8080/savesyncgroups', syncgrouppostbody, this.options);
}

public getSyncSchedule(): Observable<any[]> {
  return this.httpClient.get<any[]>('http://localhost:8080/getsyncschedule', this.options);
}
public getUserSync(): Observable<any[]> {
  return this.httpClient.get<any[]>('http://localhost:8080/getusersync', this.options);
}
public getGroupSync(): Observable<any[]> {
  return this.httpClient.get<any[]>('http://localhost:8080/getgroupsync', this.options);
}
public clearSyncConfig(): Observable<any[]> {
  return this.httpClient.post<any[]>('http://localhost:8080/clearconfig', { headers: this.headers });
}
}

export class UserObject {
  id: number;
  primaryEmail: string;
}

let usersarray: UserObject[] = []

@Injectable()
export class Service {
  saveUser() {
    let result = [];
    usersarray.forEach(elm => {
      result.push(elm);
    });
    console.log(result)
    return result;  
  }
}