import { UsersService } from './../../service/users.service';
import { Component, OnInit } from '@angular/core';
import { ClrLoadingState } from '@clr/angular';

@Component({
  selector: 'app-groups',
  templateUrl: './groups.component.html',
  styleUrls: ['./groups.component.scss']
})
export class GroupsComponent implements OnInit {

  ResponseBody
  GoogleGroups
  GoogleSearchBtn: ClrLoadingState = ClrLoadingState.DEFAULT;
  GoogleShowAllBtn: ClrLoadingState = ClrLoadingState.DEFAULT;
  AccessGroups 
  AccessSearchBtn: ClrLoadingState = ClrLoadingState.DEFAULT;
  AccessShowAllBtn: ClrLoadingState = ClrLoadingState.DEFAULT;
 
  AccessGroupName
  GoogleGroupName

  constructor(private usersService: UsersService) { }

  ngOnInit(): void {

    
  }
  onGShowAllClick() {
    this.GoogleShowAllBtn = ClrLoadingState.LOADING;
    this.usersService.getGoogleGroups()
    .subscribe(
      body => { this.GoogleGroups = body, this.GoogleShowAllBtn = ClrLoadingState.SUCCESS;},
      error => { console.log('error', error)}
      )  
  }

  onGSearchClick() {
    console.log(this.GoogleGroupName)
    let searchparam = this.GoogleGroupName
    this.GoogleSearchBtn = ClrLoadingState.LOADING;
    console.log(searchparam)
    this.usersService.searchGoogleGroups(searchparam)
    .subscribe(
      body => { this.GoogleGroups = body, this.GoogleSearchBtn = ClrLoadingState.SUCCESS; },
      error => { console.log('error', error), this.GoogleSearchBtn = ClrLoadingState.ERROR;}
      )  
  }

  onAShowAllClick() {
    this.AccessShowAllBtn = ClrLoadingState.LOADING;
    this.usersService.getAccessGroups()
    .subscribe(
      body => { this.AccessGroups = body, this.AccessShowAllBtn = ClrLoadingState.SUCCESS;},
      error => { console.log('error', error)}
      )  
  }
  onASearchClick() {
    this.AccessSearchBtn = ClrLoadingState.LOADING;
    this.usersService.searchAccessGroups(this.AccessGroupName)
    .subscribe(
      body => { this.AccessGroups = body, this.AccessSearchBtn = ClrLoadingState.SUCCESS; },
      error => { console.log('error', error)}
      )  

  }
}


