import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})

export class CredentialsService {

credentialsError;
credentilsSuccess;
isError: boolean;
isSuccess: boolean;

constructor(private httpClient: HttpClient) { }
headers = new HttpHeaders({
  'Content-Type':'application/json',
  'Accept':'application/json',
  'Access-Control-Allow-Origin': '*',
  'rejectUnauthorized':'false'
})

options = {
    observe: 'response' as 'body',
    headers: new HttpHeaders({
      'Content-Type':'application/json',
      'Accept':'application/json',
      'Access-Control-Allow-Origin': '*',
      'rejectUnauthorized':'false'
    })
  };


public getCredentials(): Observable<any[]> {
  return this.httpClient.get<any[]>('http://localhost:8080/secrets', { headers: this.headers });
}

public saveConfig(postbody): Observable<any[]> {
  return this.httpClient.post<any[]>('http://localhost:8080/saveconfig', postbody, this.options);
  }

public ping(): Observable<any[]> {
  return this.httpClient.get<any[]>('http://localhost:8080/ping', { headers: {'Access-Control-Allow-Origin': '*'} });
}

public status(): Observable<any[]> {
  return this.httpClient.get<any[]>('http://localhost:8080/status', { headers: this.headers });
}

}