import { CredentialsService } from './../service/credentials.service';
import { SaveCredentials } from './../savecredentials.model';
import { SaveCredentialsService } from './../service/savecredentials.service';
import { Component, OnInit } from '@angular/core';
import { ClrLoadingState } from '@clr/angular';
import { Router } from '@angular/router';

@Component({
  selector: 'app-config',
  templateUrl: './config.component.html',
  styleUrls: ['./config.component.scss']
})
export class ConfigComponent implements OnInit {

  Credentials
  ResponseBody
  constructor(private credentialsService: CredentialsService,  private _router: Router) { }
  isReadOnly:boolean = false
  urlPlaceholder
  domainPlaceholder
  client_idPlaceholder
  client_secretPlaceholder
  blankValue

  editBtnActive:boolean = false
  credentialsResult:boolean = false


  setPlaceholders() {
    this.urlPlaceholder = "eg. https://tenant.vmwareidentity.com"
    this.domainPlaceholder = "eg. company.com"
    this.client_idPlaceholder = "eg. access_sync"
    this.client_secretPlaceholder = "eg. secretgeneratedfromaccesstenant"

  }

  clearValues() {
    this.blankValue = ""
  }

  reloadComponent() {
    this._router.routeReuseStrategy.shouldReuseRoute = () => false;
    this._router.onSameUrlNavigation = 'reload';
    this._router.navigate(['/config']);
  }

  ngOnInit(): void {
    this.setPlaceholders()
    this.credentialsService.getCredentials()
    .subscribe(
      body => { this.Credentials = body, this.isReadOnly = true, this.urlPlaceholder = this.Credentials.url, this.domainPlaceholder = this.Credentials.domain, this.client_idPlaceholder = this.Credentials.client_id, this.client_secretPlaceholder = "*************", this.credentialsResult = true; },
      error => { console.log('error', error), this.isReadOnly = false;}
      )      

  }
  submitBtnState: ClrLoadingState = ClrLoadingState.DEFAULT;
  editBtnState: ClrLoadingState = ClrLoadingState.DEFAULT;
  cancelBtnState: ClrLoadingState = ClrLoadingState.DEFAULT;

  credentials = new SaveCredentials ('', '', '', '')
  postbody = {}
  onSubmitClick() {
    this.postbody = {

        "url": this.credentials.url,
        "client_id": this.credentials.client_id,
        "client_secret": this.credentials.client_secret,
        "domain": this.credentials.domain
    
    };
    this.submitBtnState = ClrLoadingState.LOADING;
    this.credentialsService.saveConfig(this.postbody)
    .subscribe(
      data => { console.log('success', data), this.submitBtnState = ClrLoadingState.SUCCESS, this.reloadComponent();},
      error => { console.log('error', error), this.submitBtnState = ClrLoadingState.ERROR;}
    )
  }
  
  onEditClick() {
    this.isReadOnly = false;
    this.editBtnActive = false;
    this.clearValues()
    this.setPlaceholders()
    
  }
  onCancelClick() {
    this.ngOnInit();
  }
}