import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';



@Injectable({
  providedIn: 'root'
})

export class DomainsService {
createError;
createSuccess;
isError: boolean;
isSuccess: boolean;

constructor(private httpClient: HttpClient) { }
headers = new HttpHeaders()
  .set('Content-Type', 'application/json')
  .set('Accept', 'application/json')
  .set('Access-Control-Allow-Origin', '*')
  .set('rejectUnauthorized', 'false')
options = {
    observe: 'response' as 'body',
    headers: this.headers
  };

public getDomains(): Observable<any[]> {
  return this.httpClient.get<any[]>('http://localhost:8080/accessdirs', { headers: this.headers });
}

public createDirectory(postbody): Observable<any[]> {
return this.httpClient.post<any[]>('http://localhost:8080/createdir', postbody, this.options);
}
}
