import { UsersService } from './../../service/users.service';
import { Component, OnInit } from '@angular/core';
import { ClrLoadingState } from '@clr/angular';


@Component({
  selector: 'app-users',
  templateUrl: './users.component.html',
  styleUrls: ['./users.component.scss']
})
export class UsersComponent implements OnInit {

  ResponseBody
  GoogleUsers
  GoogleSearchBtn: ClrLoadingState = ClrLoadingState.DEFAULT;
  AccessUsers 
  AccessSearchBtn: ClrLoadingState = ClrLoadingState.DEFAULT;

  constructor(private usersService: UsersService) { }

  ngOnInit(): void {

    
  }
  onGSubmitClick() {
    this.GoogleSearchBtn = ClrLoadingState.LOADING;
    this.usersService.getGoogleUsers()
    .subscribe(
      body => { this.GoogleUsers = body, this.GoogleSearchBtn = ClrLoadingState.SUCCESS; },
      error => { console.log('error', error)}
      )  

  }
  onASubmitClick() {
    this.AccessSearchBtn = ClrLoadingState.LOADING;
    this.usersService.getAccessUsers()
    .subscribe(
      body => { this.AccessUsers = body, this.AccessSearchBtn = ClrLoadingState.SUCCESS; },
      error => { console.log('error', error)}
      )  

  }
}
