import { SyncSettingsComponent } from './config/sync-settings/sync-settings.component';
import { GroupsComponent } from './tools/groups/groups.component';
import { UsersComponent } from './tools/users/users.component';
import { Routes } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { ToolsComponent } from './tools/tools.component';
import { ConfigComponent } from './config/config.component';

import { CreateDirectoryComponent } from './tools/create-directory/create-directory.component';
import { MainSidenavComponent } from './layouts/main-sidenav/main-sidenav.component';
import { MainComponent } from './layouts/main/main.component';




const appRoutes: Routes = [
    {
      path: '',
      component: MainComponent,
      children: [
        { path: '', component: HomeComponent, pathMatch: 'full' },
      ]
    },
    {
      path: '',
      component: MainSidenavComponent,
      children: [
        { path: 'tools', component: ToolsComponent },
        { path: 'tools/create-directory', component: CreateDirectoryComponent},
        { path: 'tools/users', component: UsersComponent},
        { path: 'tools/groups', component: GroupsComponent},
        { path: 'config', component: ConfigComponent},
        { path: 'config/sync-settings', component: SyncSettingsComponent}

      ]
    },
  ];
export default appRoutes;
